<template>
  <div class="index">
    <div class="left">
      <ul class="leftUl">
        <li v-for="(item, index) in rightData" :key="index">{{item}}</li>
      </ul>
    </div>
  </div>
</template>

<script>
import { Alert } from 'zms-cj'
import Vue from 'vue'
Vue.use(Alert)
export default {
  name: 'HelloZmscj',
  data () {
    return {
      rightData: [
        'zmscj介绍',
        '引用',
        'Loading',
        'Alert',
        'LoadingMiddle',
        'Toast'
      ]
    }
  }
}
</script>
<style lang="scss">
.index {
  width: 100%;
  height: 100%;
  padding: 10px 0;
  box-sizing: border-box;
  .left {
    width: 260px;
    height: 100%;
    background: #fff;
    border: 1px solid #efefef;
    box-shadow:  0 2px 2px #efefef;
    font-size: 16px;
    padding: 20px;
    box-sizing: border-box;
    .leftUl {
      width: 100%;
      & > li {
        width: 100%;
        height: 50px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        font-size: 16px;
        &:hover {
          color: #00a1a3;
          cursor: pointer;
          font-weight: bold;
        }
      }
    }
  }
}
</style>



